.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 1100px;
  margin: auto;
  overflow:hidden;
  border-radius: 0.4rem;
}
.options {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  background-color: cadetblue;
  border-radius: 0.4rem;
  width: 100%;
}

.options > div { 
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: 0.5rem 1rem;
}

.options label {
  margin: 0.3rem;
}
.options > div > *:nth-child(2) {
  border: 1px solid blue;
  font-size: 1.2rem;
  border: none;
  border-radius: 0.4rem;
  padding: 0.4rem;
  color: #555;
}

.searchdiv {
    padding: 0.3rem;
    .content {
      font-size: 1.125em;
      line-height: 1.6;
      --clr-primary: #ee6352;
      --clr-body: #eee;
      --clr-bg: #ddd;
      position: relative;
    }
    
    .search {
        margin-left: auto;
      --size: 30px;
      border: 2px solid var(--clr-primary);
      display: flex;
      border-radius: 100vh;
      overflow: hidden;
      font-size: inherit;
      position: relative;
      width: var(--size);
      height: var(--size);
      padding: 3px;
      
      @media (prefers-reduced-motion: no-preference) {
          transition: width 550ms cubic-bezier(0.18, 0.89, 0.22, 1.18);
      }
    }
    
    .search__input {
      border: 0;
      padding: .25em 1em;
      flex-grow: 1;
      outline: 0;
      z-index: 2;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      width: calc(100% - 2 * var(--size));
      background: transparent;
      opacity: 0;
      cursor: pointer;
    }
    
    .search__submit {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: auto;
        background: 0;
        border: 0;
        cursor: pointer;
        border-radius: 50%;
        transition: background 200ms ease-out;
        width: var(--size);
        height: var(--size);
        color: white;
    }
    
    .search:focus-within {
      width: 90%;
      flex-grow: 2;
      
      .search__input {
        opacity: 1;
        z-index: initial;
        cursor: initial;
      }
      
      .search__submit {
        background: var(--clr-primary);
        color: white;
        box-shadow: 0 0 10px rgba(0,0,0,.15);
        
        &:hover,
        &:focus {
          outline: 0;
          box-shadow: 0 0 10px rgba(0,0,0,.65);
        }
      }
    }
}

.table {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0.5rem;
  margin: 1rem;
  margin-bottom: 3rem;
  width: 98%;
}

.row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 98%;
  font-size: 0.8rem;
  border-radius: 0.4rem;
  overflow: hidden;
}

.row > div {
  margin: 0;
  padding: 0.2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  align-items: center;
}

.name {
  color: darkgoldenrod;
}
