}

@media (max-width: 980px) {
  .table {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .table {
    grid-template-columns: repeat(1, 1fr);
  }
  .options {
    flex-direction: column;
  }
}

